import util from '@/utils/utils.js'
import service from '@/service/service.js'
export default {
	data() {
		return {
			tabletwoData:[],
			show:false,
			submitform:{
				user:'',
				name:'',
				jhy:'',
				hy:''
			},
			list:[],
			shujuData:'',
			loading:true,
			searchform:{
				cityname:'',
				sou:'',
				sousuo:''
			},
			zhfl:[
				{
					value: '1',
					name: '顺序',
					id: 1
				},
				{
					value: '2',
					name: '倒序',
					id: 2
				},
			],
			sml:[
				{
					value: '1',
					name: '顺序',
					id: 1
				},
				{
					value: '2',
					name: '倒序',
					id: 2
				},
			],
			qb:'',
			qw:'',
			disabled1:false,
			disabled2:false,
		}
	},
	created() {
		this.cityxinxi(1)
		
	},
	methods: {
		zhflclick(){
			this.disabled1 = false
			this.disabled2 = true
		},
		smlclick(){
			this.disabled1 = true
			this.disabled2 = false
		},
		async cityxinxi(page){
			const cityxinxi = await service.cityxinxi({
				page:page,
				chengshi:this.searchform.cityname,
				huoquedu:this.searchform.sou,
				shiming:this.searchform.sousuo
			});
			if(cityxinxi.data.status == 1){
				this.loading = true
				this.shujuData = cityxinxi.data.data.num
				this.tabletwoData = cityxinxi.data.data.list
				this.loading = false
			}else{
				this.loading = false   
				this.$message.error(cityxinxi.data.info); 
			}
		},
		clone(){
			this.show = false
		},
		async lookClick(row){
			this.show = true
			// this.list = row.user
			const cityMemeber = await service.cityMemeber({
				city:row.chengshi_name
			})
			if(cityMemeber.data.status == 1){
				this.list = cityMemeber.data.data
			}
		},
		//搜索
		onSearchClick(){
			this.cityxinxi(1)
		},
		// 分页
		handleCurrentChange(val) {
			this.cityxinxi(val)
		},
	}
}



